.pt-breadcrumbs {
  display: inline-block;
  margin: 0;
  cursor: default;
  padding: 0;
  list-style: none;
  vertical-align: top;
  line-height: 30px; }
  .pt-breadcrumbs > li {
    float: left; }
    .pt-breadcrumbs > li::after {
      line-height: 1;
      font-family: "Icons16", sans-serif;
      font-size: 16px;
      font-weight: 400;
      font-style: normal;
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      padding: 0 5px;
      color: #5c7080;
      content: ""; }
    .pt-breadcrumbs > li:last-child::after {
      display: none; }

.pt-breadcrumb,
.pt-breadcrumb-current,
.pt-breadcrumbs-collapsed {
  display: inline-block;
  line-height: 19px;
  font-size: 16px; }

.pt-breadcrumb,
.pt-breadcrumbs-collapsed {
  color: #5c7080; }

.pt-breadcrumb:hover {
  text-decoration: none; }

.pt-breadcrumb.pt-disabled {
  cursor: not-allowed;
  color: rgba(92, 112, 128, 0.5); }

.pt-breadcrumb-current {
  color: inherit;
  font-weight: 600; }
  .pt-breadcrumb-current .pt-input {
    vertical-align: baseline;
    font-size: inherit;
    font-weight: inherit; }

.pt-breadcrumbs-collapsed {
  margin-right: 2px;
  border: none;
  border-radius: 3px;
  background: #ced9e0;
  cursor: pointer;
  padding: 0 5px; }
  .pt-breadcrumbs-collapsed::before {
    line-height: 1;
    font-family: "Icons20", sans-serif;
    font-size: 20px;
    font-weight: 400;
    font-style: normal;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    line-height: 19px;
    content: ""; }
  .pt-breadcrumbs-collapsed:hover {
    background: #bfccd6;
    text-decoration: none;
    color: #182026; }

.pt-dark .pt-breadcrumb,
.pt-dark .pt-breadcrumbs-collapsed {
  color: #bfccd6; }

.pt-dark .pt-breadcrumbs > li::after {
  color: #bfccd6; }

.pt-dark .pt-breadcrumb.pt-disabled {
  color: rgba(191, 204, 214, 0.5); }

.pt-dark .pt-breadcrumb-current {
  color: #f5f8fa; }

.pt-dark .pt-breadcrumbs-collapsed {
  background: rgba(16, 22, 26, 0.4); }
  .pt-dark .pt-breadcrumbs-collapsed:hover {
    background: rgba(16, 22, 26, 0.6);
    color: #f5f8fa; }

