.pt-callout {
  line-height: 1.5;
  font-size: 14px;
  position: relative;
  border-radius: 3px;
  background-color: rgba(138, 155, 168, 0.15);
  padding: 10px 12px 9px; }
  .pt-callout[class*="pt-icon-"] {
    padding-left: 40px; }
    .pt-callout[class*="pt-icon-"]::before {
      line-height: 1;
      font-family: "Icons20", sans-serif;
      font-size: 20px;
      font-weight: 400;
      font-style: normal;
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      position: absolute;
      top: 10px;
      left: 10px;
      color: #5c7080; }
  .pt-callout h5 {
    margin-top: 0;
    margin-bottom: 5px;
    line-height: 20px; }
  .pt-dark .pt-callout {
    background-color: rgba(138, 155, 168, 0.2); }
    .pt-dark .pt-callout[class*="pt-icon-"]::before {
      color: #bfccd6; }
  .pt-callout.pt-intent-primary {
    background-color: rgba(19, 124, 189, 0.15); }
    .pt-callout.pt-intent-primary[class*="pt-icon-"]::before {
      color: #137cbd; }
    .pt-callout.pt-intent-primary h5 {
      color: #106ba3; }
    .pt-dark .pt-callout.pt-intent-primary {
      background-color: rgba(19, 124, 189, 0.25); }
