  font-size: 14px;
  font-weight: 400;
  transition: box-shadow 100ms cubic-bezier(0.4, 1, 0.75, 0.9); }
  .pt-input::-webkit-input-placeholder {
    opacity: 1;
    color: rgba(92, 112, 128, 0.5); }
  .pt-input::-moz-placeholder {
    opacity: 1;
    color: rgba(92, 112, 128, 0.5); }
  .pt-input:-moz-placeholder {
    opacity: 1;
    color: rgba(92, 112, 128, 0.5); }
  .pt-input:-ms-input-placeholder {
    opacity: 1;
