.docs-icons .pt-input-group {
  margin: 50px 0; }

.docs-icons .docs-icon-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 50px; }
  .docs-icons .docs-icon-group h3 {
    flex: 1 1 100%;
    margin: 0 0 50px;
    text-transform: capitalize; }

.docs-icon {
  position: relative;
  cursor: pointer; }
  .docs-icon::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform: scale(1);
    opacity: 0;
    border-radius: 6px;
    background: #ebf1f5;
    content: "";
    transition: transform 100ms cubic-bezier(0.4, 1, 0.75, 0.9), opacity 100ms cubic-bezier(0.4, 1, 0.75, 0.9); }
  .docs-icon:hover::before, .pt-popover-open .docs-icon.docs-version-selector::before {
    transform: scale(1.2);
    opacity: 1; }
  .docs-icon:active::before {
    background-color: #e1e8ed; }
  .docs-icon .pt-icon-large,
  .docs-icon .docs-icon-svg {
    position: relative; }
  .pt-dark .docs-icon::before {
    background-color: #293742; }
  .pt-dark .docs-icon:active::before {
    background-color: #202b33; }

.docs-icon,
.docs-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px 0;
  border-radius: 3px;
  width: 19%;
  padding: 20px 0;
  transition: background-color 100ms cubic-bezier(0.4, 1, 0.75, 0.9); }
  .docs-icon .docs-icon-svg,
  .docs-placeholder .docs-icon-svg {
    max-width: 36px;
    max-height: 36px; }
  .docs-icon input,
  .docs-placeholder input {
    cursor: pointer; }
  .docs-icon:hover .docs-icon-name, .pt-popover-open .docs-icon.docs-version-selector .docs-icon-name,
  .docs-placeholder:hover .docs-icon-name, .pt-popover-open .docs-placeholder.docs-version-selector .docs-icon-name {
    transform: translateY(-14px);
