.docs-flex-row {
  display: flex;
  flex-direction: row; }

.docs-flex-column {
  display: flex;
  flex-direction: column; }

/*
Page layout elements
*/
.docs-root {
  background-color: #f5f8fa;
  min-height: 100vh; }
  .docs-root.pt-dark {
    background-color: #293742; }

.docs-app {
  margin: auto;
  width: 1000px;
  padding-top: 50px; }

.docs-navbar {
  position: fixed;
  top: 0;
  width: inherit; }

.docs-content {
  margin-left: 220px;
  padding: 0 10px 30px 30px; }

.docs-nav {
  position: fixed;
  top: 50px;
  bottom: 0;
  width: 220px;
  overflow: auto;
  padding: 30px 10px; }

/*
Lefthand navigation menu
*/
.docs-nav-menu .docs-nav-menu {
  display: none; }

.docs-nav-expanded > .docs-nav-menu {
  display: block; }

.docs-nav-expand-all .docs-nav-menu {
  display: block !important; }

.docs-empty-state {
  display: none; }

.docs-nav-menu:empty + .docs-empty-state {
  display: block; }

.docs-menu-item .pt-menu-item {
  padding-right: 10px;
  padding-left: 10px;
  white-space: initial; }

.docs-menu-item .pt-menu-item:not(.pt-active):hover, .docs-menu-item .pt-popover-open .pt-menu-item.docs-version-selector:not(.pt-active), .pt-popover-open .docs-menu-item .pt-menu-item.docs-version-selector:not(.pt-active) {
  background-color: rgba(167, 182, 194, 0.3);
  text-decoration: none;
  color: #182026; }

.docs-menu-item .pt-menu-item:not(.pt-active):active {
  background-color: rgba(115, 134, 148, 0.3); }

.pt-dark .docs-menu-item .pt-menu-item:not(.pt-active) {
  color: #f5f8fa; }
  .pt-dark .docs-menu-item .pt-menu-item:not(.pt-active):hover, .pt-dark .docs-menu-item .pt-popover-open .pt-menu-item.docs-version-selector:not(.pt-active), .pt-popover-open .pt-dark .docs-menu-item .pt-menu-item.docs-version-selector:not(.pt-active) {
    box-shadow: none;
    background-color: rgba(138, 155, 168, 0.15);
    color: #f5f8fa; }
  .pt-dark .docs-menu-item .pt-menu-item:not(.pt-active):active {
    background-color: rgba(138, 155, 168, 0.3); }

.docs-menu-item.depth-2 > .pt-menu-item {
  padding-left: 30px; }

.docs-menu-item.depth-3 > .pt-menu-item {
  padding-left: 50px; }

.docs-menu-item.depth-4 > .pt-menu-item {
  padding-left: 70px; }

.docs-menu-item.depth-5 > .pt-menu-item {
  padding-left: 90px; }

